body {
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-color: #333;
    margin: 0;
    padding: 0;
}

.grid {
    display: grid;
    grid-template-columns: repeat(3, 20px);
    gap: 5px;
}

.square {
    width: 20px;
    height: 20px;
    background-color: #FFFF;
    opacity: 1;
    transform-origin: center;
    animation: waveAnimation 1.7s infinite;
}

@keyframes waveAnimation {
    0%, 100% {
        transform: scale(1);
    }
    15% {
        transform: scale(0);
    }
    55% {
        transform: scale(0);
    }
    75% {
        transform: scale(1);
    }
}

.square[data-id="1"] { animation-delay: 0s; }

.square[data-id="2"], .square[data-id="4"] { animation-delay: 0.1s; }

.square[data-id="3"], .square[data-id="5"], .square[data-id="7"] { animation-delay: 0.2s; }

.square[data-id="6"], .square[data-id="8"] { animation-delay: 0.3s; }

.square[data-id="9"] { animation-delay: 0.4s; }
